.repository {
    padding: 20px;
    margin: 0;
    list-style: none;
    color: #ffffff;
    height: 100%;
    background: #67b26f;
    background: -webkit-gradient(linear, left top, left bottom, from(#4ca2cd), to(#67b26f));
    background: -webkit-linear-gradient(top, #4ca2cd, #67b26f);
    background: -o-linear-gradient(top, #4ca2cd, #67b26f);
    background: linear-gradient(to bottom, #4ca2cd, #67b26f);
    -webkit-transition: all .25s linear;
    -o-transition: all .25s linear;
    transition: all .25s linear;
}

.repository:hover {
    -webkit-box-shadow: 0 14px 28px rgba(76,162,205,.25), 0 10px 10px rgba(103,178,110,.22);
            box-shadow: 0 14px 28px rgba(76,162,205,.25), 0 10px 10px rgba(103,178,110,.22);
}

.repository__link,
.repository__link:visited,
.repository__link:active,
.repository__link:link {
    color: #ffffff;
    text-decoration: none;
    overflow: hidden;
    -ms-flex-pack: justify;
        justify-content: space-between;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: 100%;
    -ms-flex-direction: column;
        flex-direction: column;
}

.repository__link:hover {
    color: #ffffff;
    text-decoration: none;
}

.repository__title {
    font-size: 32px;
    margin: 0;
}

.repository__star {
    background: rgba(255,255,255,.3);
    padding: 5px 10px;
    border-radius: 20px;
    display: -ms-flexbox;
    display: flex;
    color: #ffffff;
    height: 33px;
    -ms-flex-align: center;
        align-items: center;
}

.repository__star__icon_star {
    width: 16px;
    height: 16px;
    margin-right: 5px;
}

@media (min-width: 750px) {
    .repository {
        min-height: 300px;
    }
}



